******************************/
 @keyframes fx-overlay {
    0% {
        visibility: visible;
        color: transparent;
    }
    50% {
        background-color: darken(@darkblue2, 10%);
        color: white;
    }
    100% {
        visibility: visible;
        background-color: darken(@darkblue2, 10%);
        color: #FFF;
    }
}
@keyframes fx-overlay-reverse {
    0% {
        opacity: 1;
        visibility: visible;
        background-color: darken(@darkblue2, 10%);
        color: #FFF;
    }
    27% {
        opacity: 1;
        color: transparent;
    }
    50% {
        opacity: 1;
    }
    94% {
        opacity: 0;
        visibility: visible;
        background-color: darken(@darkblue2, 10%);
        color: transparent;
    }
    95% {
        visibility: hidden;
        opacity: 0;
    }
    100% {
        visibility: hidden;
        opacity: 1;
    }
}
